import $ from 'jquery';
import { getPercent, sticky } from '../sticky';
import { model } from '../three/model';

window.addEventListener('scroll', () => {
    // activeScroll();
});

let current = -1;
const activeScroll = () => {
    const workList = document.querySelector('.page-work .work-list');
    const workItem = workList.querySelectorAll('.work-item');
    let windowVerticalStart = (window.innerHeight - workItem[0].clientHeight) * 0.5;
    let windowVerticalEnd = windowVerticalStart +  workItem[0].clientHeight;
    $(workItem).each(function(i, ele) {
        let itemY = ele.getBoundingClientRect().y;
        if( itemY > windowVerticalStart && itemY < windowVerticalEnd) {
            if($(ele).find('a').hasClass('active')) return;
            
            $(workItem).find('a').removeClass('active');
            $(ele).find('a').addClass('active');

            current = i;
        } else if (itemY < windowVerticalStart || workItem[0].getBoundingClientRect().y > windowVerticalEnd) {
            $(workItem).find('a').removeClass('active');
            current = -1;
        }
    });
    model.changeBoard(current);
}

sticky('.sticky-work', ($this, percent) => {
    let moveY = $this.find('.work-list').height() * percent * -1;
    $this.find('.work-list').css({ 'transform' : 'translateY('+moveY+'px)'});
});
