import $ from 'jquery'
import * as THREE from 'three'
import { DRACOLoader } from 'three/examples/jsm/loaders/DRACOLoader.js'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'

let dracoLoader, gltfLoader, textureLoader

const prop = {
    color: {
        background: 0x363640,
        building: 0xC7C7C7,
        block: 0xC7C7C7,
        tree: 0x29661B,
        park: 0x278212,
        fave: 0x7848C2,
        board: 0x000000,
    },
    material: {
        metalness: 0.1,
        roughness: 0.9,
        shininess: {
            building: 30,
            tree: 30,
            block: 30,
            board: 20,
        },
        specular: 0x333333,
    },
    project: { 
        items: '.page-work .work-item',
        imgPath: './resources/images/project/',
        covers: [],
    },
    board: null
}

export const model = {
    init: function(loadingManager) {
        dracoLoader = new DRACOLoader(loadingManager)
        dracoLoader.setDecoderPath('https://www.gstatic.com/draco/versioned/decoders/1.4.1/')
        dracoLoader.setDecoderConfig({type: 'js'})
        dracoLoader.preload()

        gltfLoader = new GLTFLoader(loadingManager)
        gltfLoader.setDRACOLoader(dracoLoader)
        
        textureLoader = new THREE.TextureLoader()

        $(prop.project.items).each((i) => {
            prop.project.covers.push(textureLoader.load(prop.project.imgPath + `project-cover_${i + 1}.jpg`))
        })
    },

    changeBoard: function(n) {
        if (prop.board) {
            prop.board.material.dispose()
            if (n == -1) {
                prop.board.material = new THREE.MeshPhongMaterial({
                    color: prop.color.board,
                    side: THREE.DoubleSide,
                    specular: prop.material.specular,
                    shininess: prop.material.shininess.board,
                })
            } else {
                prop.board.material = new THREE.MeshBasicMaterial({ map: prop.project.covers[n] })
            }
        }
    }
}

export const office = {
    file: './resources/models/office.glb',
    position: new THREE.Vector3(40.1, 2, 1.91),
    rotation: 0.005,
    scale: 5.65,

    load: function(scene) {
        gltfLoader.load(this.file, (gltf) => {
            const model = gltf.scene
            model.scale.set(this.scale, this.scale, this.scale)
            model.position.copy(this.position)
            model.rotation.y = this.rotation
            model.frustumCulled = false
            model.receiveShadow = true
            model.children.forEach((element) => {
                if (element.name == 'screen') { 
                    prop.board = element.children[0]
                }

                element.traverse((node) => {
                    if (node.isMesh) {
                        node.frustumCulled = false
                        node.material.metalness = prop.material.metalness
                        node.material.roughness = prop.material.roughness
                    }
                })
            })
            scene.add(model)
        })
    }
}

export const city = {
    file: './resources/models/city.gltf',
    position: new THREE.Vector3(0, 0, 0),
    scale: 0.1,

    load: function(scene) {
        gltfLoader.load(this.file, (gltf) => {
            const model = gltf.scene
            model.scale.set(this.scale, this.scale, this.scale)
            model.position.copy(this.position)
            model.frustumCulled = false
            model.receiveShadow = true
            model.children[0].children.forEach(element => {
                let color, 
                    shininess = 0, 
                    castShadow = true,
                    receiveShadow = true
                switch (element.name) {
                    case 'Building':
                        color = prop.color.building
                        shininess = prop.material.shininess.building
                        break
                    case 'Tree':
                        color = prop.color.tree
                        shininess = prop.material.shininess.tree
                        break
                    case 'Park':
                        color = prop.color.park
                        shininess = 1
                        break
                    case 'Fave':
                        color = prop.color.fave
                        break
                    case 'Block':
                        color = prop.color.block
                        shininess = prop.material.shininess.block
                        break
                    // case 'Board':
                    //     color = prop.color.board
                    //     shininess = prop.material.shininess.board
                    //     break
                    case 'Screen':
                        color = prop.color.board
                        prop.board = element.children[0]
                        castShadow = false
                        receiveShadow = false
                        break
                    default:
                        color = 0xff0000
                        break
                }

                element.traverse((node) => {
                    if (node.isMesh) {
                        node.geometry.computeVertexNormals()
                        node.castShadow = castShadow
                        node.receiveShadow = receiveShadow
                        node.frustumCulled = false
                        node.material = new THREE.MeshPhongMaterial({
                            color: color,
                            side: THREE.DoubleSide,
                            specular: prop.material.specular,
                            shininess: shininess,
                        })
                    }
                })
            })
            // model.matrixAutoUpdate = false
            // prop.model.city && scene.remove(prop.model.city)
            // prop.model.city = model
            scene.add(model)
        })
    }
}