import $ from 'jquery'
import gsap from 'gsap'
import { load as loadThree, resize as resizeThree, scroll as scrollThree } from './three/load'
import { loadCursor } from './cursor'

window.FAVE = window.FAVE || {
    window: $(window),
    areaWidth: $(window).outerWidth(),
    areaHeight: $(window).outerHeight(),
    scrollContent: null,
    scrollParent: null,
    mouse: { x: 0, y: 0},
    scrollTop: 0,
    scrollFrame: null,
    scrollDuration: 0.85,
    scrolling: false,
    stickys: null,
    scroller: {
        ease: 1,
        endY: 0,
        y: 0,
        scrolled: true,
        timeout: null
    },

    load: function() {
        this.scrollContent = $('.page')
        this.stickys = $('.sticky-wrap')
        $('.scroll-height').css({ height: FAVE.scrollContent.outerHeight() })

        loadThree()
        // loadCursor()

        gsap.ticker.fps(0)
        this.scroller.ease = Math.round((1 - this.scrollDuration) * 100) / 100

        $('html, body, main').scrollTop(0)
        this.scroll()
        this.resize()
    },

    resize: function() {
        this.areaWidth = $(window).outerWidth()
        this.areaHeight = $(window).outerHeight()
        this.scroll()
        resizeThree()
    },

    scrollUpdate: function() {
        let scrollY = FAVE.window.scrollTop()

        FAVE.scroller.ease = (1 - FAVE.scrollDuration)
        FAVE.scroller.endY = scrollY
        FAVE.scroller.y += (scrollY - FAVE.scroller.y) * FAVE.scroller.ease

        if (Math.abs(scrollY - FAVE.scroller.y) < FAVE.scroller.ease) {
            FAVE.scroller.y = scrollY
            FAVE.scroller.scrolled = false
            gsap.ticker.remove(FAVE.scrollUpdate)
        }
        
        FAVE.scroller.timeout = (FAVE.scroller.scrolled) ? gsap.ticker.add(FAVE.scrollUpdate) : FAVE.scroller.timeout = null
        
        FAVE.scrollContent.css({ transform: `translate3d(0, ${-FAVE.scroller.y}px, 0)` })
    },

    scroll: function() {
        FAVE.scrollTop = FAVE.window.scrollTop()
        FAVE.scroller.scrolled = true
        if (FAVE.scroller.timeout === null) FAVE.scroller.timeout = gsap.ticker.add(FAVE.scrollUpdate)
        scrollThree(FAVE.scrollTop)
    },

    bodyScroll: function (y) {
        FAVE.scrollContent.css({ transform: `translate3d(0, ${-y}px, 0)` })
        FAVE.stickyScroll(y)
    },

    stickyScroll: function (y) {
        this.stickys.each(function (idx, _this) {
            let stickyCon = _this.querySelector('.sticky-content')
            let content = $(_this).find('.sticky-content')
            let _move, _percent, _percent2

            _percent = Math.max(0, Math.min(1, (y - content.offset().top) / (content.height() - FAVE.areaHeight)))
            _percent2 = Math.max(0, Math.min(1, (y - content.offset().top) / (content.height())))
            
            
            const p = _this.getBoundingClientRect().top * -1
            _move = Math.min(_this.clientHeight - window.innerHeight, Math.max(0, _this.getBoundingClientRect().top * -1));
            stickyCon.style.transform = 'translate3d(0,' + _move + 'px,0)'
            
            if (p > 0 && p <= _this.clientHeight - window.innerHeight) {
                !$(_this).hasClass('sticky-on') && $(_this).addClass('sticky-on')
            } else {
                $(_this).hasClass('sticky-on') && $(_this).removeClass('sticky-on')
            }
        });
    },

    mousemove: function(e) {
        this.mouse = {
            x: e.clientX,
            y: e.clientY
        }
        // threejs render
    }
}

$(window)
    .on('load', () => {
        FAVE.load()
    })
    .on('resize', () => {
        FAVE.resize()
    })
    .on('scroll', () => {
        FAVE.scroll()
    })
    .on('mousemove', (e) => {
        FAVE.mousemove(e)
    })