        camera.add(look)
        const updateCamera = () => {
            camera.zoom = FAVE.three.camera.zoom
            camera.fov = FAVE.three.camera.fov
            camera.aspect = FAVE.three.camera.aspect
            camera.updateProjectionMatrix()
        }

