        cameraFolder.add(FAVE.three.camera.position, 'y').name('position Y').min(0).max(10).step(0.01).onChange(() => { updateCamera() })
        cameraFolder.add(FAVE.three.camera.position, 'z').name('position Z').min(-30).max(30).step(0.01).onChange(() => { updateCamera() })

