        cameraFolder.add(FAVE.three.camera.look, 'y').name('look Y').min(-10).max(100).step(0.01).onChange(() => { updateCamera() })
        cameraFolder.add(FAVE.three.camera.look, 'z').name('look Z').min(-50).max(50).step(0.01).onChange(() => { updateCamera() })
