        const renderScene = () => {
            clearTimeout(FAVE.three.render.interval)
            FAVE.three.render.active = true
            FAVE.three.render.interval = setTimeout(() => {
                FAVE.three.render.active = false
            }, FAVE.three.render.time)
        }

