import $ from 'jquery'
import * as THREE from 'three'

export const cameraScroll = {
    active: false,
    page: {
        el: null,
        index: 0,
    },
    points: 200,
    size: 0,
    camMoves: null,
    camLooks: null,

    createRoute: function() {
        const group = new THREE.Group()

        const movePoints = this.camMoves.getSpacedPoints(this.points)
        const lookPoints = this.camLooks.getSpacedPoints(this.points)

        const moveLine = new THREE.Line(
            new THREE.BufferGeometry().setFromPoints(movePoints),
            new THREE.LineBasicMaterial({ color : 0xff0000 })
        )
        const lookLine = new THREE.Line(
            new THREE.BufferGeometry().setFromPoints(lookPoints),
            new THREE.LineBasicMaterial({ color : 0x0000ff })
        )

        group.add(moveLine)
        group.add(lookLine)

        const sphereGeomtry = new THREE.SphereBufferGeometry(0.03),
            materialRed = new THREE.MeshBasicMaterial( { color: 0xff0000 } ),
            materialBlue = new THREE.MeshBasicMaterial( { color: 0x0000ff } )
            
        const moveSpots = this.camMoves.getPoints(FAVE.three.camera.moves.length - 1)
        const lookSpots = this.camLooks.getPoints(FAVE.three.camera.looks.length - 1)

        for (let i = 0; i < moveSpots.length; i ++) {
            const moveHelper = new THREE.Mesh(sphereGeomtry, materialRed)
            const lookHelper = new THREE.Mesh(sphereGeomtry, materialBlue)
            moveHelper.position.copy(moveSpots[i])
            lookHelper.position.copy(lookSpots[i])

            group.add(moveHelper)
            group.add(lookHelper)
        }

        return group
    },

    init: function($el, debug = false)  {
        this.page.el = $el
        if (this.page.el.length) this.active = true

        this.camMoves = new THREE.CatmullRomCurve3(FAVE.three.camera.moves)
        this.camLooks = new THREE.CatmullRomCurve3(FAVE.three.camera.looks)

        this.size = 1 / FAVE.three.camera.moves.length

        const io = new IntersectionObserver((entries) => {
            const entry = entries[0]
            if (!entry.isIntersecting) console.log('current:', entry.target)
        })
        this.page.el.each((i, page) => {
            io.observe(page)
        })

        if (debug) debug(this.createRoute())
    },
    
    scroll: function(y, camera) {
        if (this.active) {
            let start, end, percent = 0
            
            this.page.el.each((i, page) => {
                const posTop = $(page).position().top
                const height = $(page).height()

                if (y >= posTop && y < posTop + height) {
                    // console.log(i, y / (posTop + height))
                    start = FAVE.three.camera.steps[i].start * this.size
                    end = FAVE.three.camera.steps[i].end * this.size
                    percent = (y - $(page).position().top) / (height - (i == FAVE.three.camera.steps.length - 1 ? FAVE.areaHeight : 0))
                    FAVE.three.move.index = (start + (end - start) * percent) * FAVE.three.move.speed
                    // console.log('section:', i)
                    this.update(camera)
                }
            })
        }
    },

    update: function(camera) {
        const pos = FAVE.three.move.index / FAVE.three.move.speed
        const camPos = this.camMoves.getPoint(pos)
        const lookPos = this.camLooks.getPoint(pos)
        FAVE.three.camera.position.copy(camPos)
        FAVE.three.camera.lookNext.copy(lookPos)
        // FAVE.three.camera.look.copy(lookPos)
        // camera.position.copy(FAVE.three.camera.position)
    },
}