import $ from 'jquery'

export const getPercent = (percent, start, length) => {
    const p = (percent - (1 * start)) / (1 * length)
    return Math.max(0, Math.min(1, p))
}

export const sticky = (el, scroll) => {
    const $window = $(window)

    $window.on('load', () => {
        const $wrap = $(el)
        if (!$wrap.length) return
        
        $window.on('scroll', () => {
            const position = $window.scrollTop() - $wrap.offset().top
            const range = $wrap.height() - $window.outerHeight()
            const percent = position / range
            
            if (percent > -0.1 && percent < 1.1) {
                scroll($wrap, Math.max(Math.min(percent, 1), 0))
            }
        })
    })
}