import $ from 'jquery'

const prop = {
    elements:   [ '.cursor-inner', '.cursor-outer' ],
    cursors:    [],
    mouse:      { x: 0, y: 0 },
    lerp:       [ 0.5, 0.18 ],
    rotate:     2,
}

class Cursor {
    constructor($el, index) {
        this.$el = $el
        this.x = 0
        this.y = 0
        this.index = index || 0
        this.rotate = 0
        this.delay = prop.lerp[index] || 0
    }
    
    move = () => {
        let next = FAVE.mouse
        if (this.index != 0) {
            next = { 
                x: prop.cursors[this.index - 1].x,
                y: prop.cursors[this.index - 1].y
            }
        }

        this.x = this.lerp(this.x, next.x, this.delay)
        this.y = this.lerp(this.y, next.y, this.delay)
        this.rotate += prop.rotate
        if (this.rotate == 360) this.rotate = 0

        this.$el.css({
            top: this.y,
            left: this.x,
        });
    }
    
    lerp = (start, end, amt) => {
        return (1 - amt) * start + amt * end
    }
}

export const loadCursor = () => {
    const animate = () => {
        prop.cursors.forEach((c) => {
            c.move()
        })

        window.requestAnimationFrame(animate)
    }

    const init = () => {
        prop.elements.forEach((el, i) => {
            const $el = $(el)
            if ($el.length) prop.cursors.push(new Cursor($el, i))
        })
        
        if (prop.cursors.length) {
            animate()
        }
    }
    init()
}


export const hoverCursor = ($el) => {
    // console.log($el.data('hover'))
}

$(window).on('load', () => {
    $('a, button').each((index, e) => {
        $(e).on('mouseover', () => {
            hoverCursor($(e))
        })
    })
})