import { sticky } from '../sticky';
import $ from 'jquery';
import { googleMap } from '../three/load';

const resizeMap = () => {
    $('.google-map iframe').attr('width', FAVE.areaWidth).attr('height', FAVE.areaHeight)
}

$(window).on('load', () => {
    resizeMap()
    $('.btn-map').on('click', () => {
        googleMap()
        if (FAVE.three.map.on) {
            $('html').removeClass('scroll-on')
            $('.google-map').addClass('map-on')
        } else {
            $('html').addClass('scroll-on')
            $('.google-map').removeClass('map-on')
        }
    })
})

$(window).on('resize', () => {
    resizeMap()
})