import $ from 'jquery'
import * as THREE from 'three'

export const light = {
    ambient: {
        intensity: 0.35,
        color: 0x808CEF
    },
    direct1: {
        intensity: 1.9,
        color: 0xFF9444,
        size: 3790,
        position: {
            x: 167,
            y: 21,
            z: 31,
        }
    },
    direct2: {
        intensity: 0.4,
        color: 0xFF9444,
        size: 3790,
        position: {
            x: 157,
            y: 11,
            z: -42,
        }
    },

    init: function(callback, debug = false) {
        const lights = []

        const ambientLight = new THREE.AmbientLight(
            this.ambient.color, 
            this.ambient.intensity
        )
        lights.push(ambientLight)
        
        const directLight1 = new THREE.DirectionalLight(
            this.direct1.color, 
            this.direct1.intensity
        )
        directLight1.castShadow = true
        directLight1.shadow.mapSize.set(this.direct1.size, this.direct1.size)
        directLight1.shadow.camera.near = 0.1
        directLight1.shadow.camera.far = 400
        directLight1.shadow.camera.left = -500
        directLight1.shadow.camera.top = 500
        directLight1.shadow.camera.right = 500
        directLight1.shadow.camera.bottom = -500
        directLight1.shadow.radius = 10
        directLight1.shadow.bias = -0.00001
        directLight1.position.x = this.direct1.position.x
        directLight1.position.y = this.direct1.position.y
        directLight1.position.z = this.direct1.position.z
        directLight1.target.position.set(0, 0, 0)
        lights.push(directLight1)
        lights.push(directLight1.target)
        
        const directLight2 = new THREE.DirectionalLight(
            this.direct2.color, 
            this.direct2.intensity
        )
        directLight2.castShadow = false
        directLight2.shadow.mapSize.set(this.direct2.size, this.direct2.size)
        directLight2.shadow.camera.near = 0.1
        directLight2.shadow.camera.far = 400
        directLight2.shadow.camera.left = -500
        directLight2.shadow.camera.top = 500
        directLight2.shadow.camera.right = 500
        directLight2.shadow.camera.bottom = -500
        directLight2.position.x = this.direct2.position.x
        directLight2.position.y = this.direct2.position.y
        directLight2.position.z = this.direct2.position.z
        directLight2.target.position.set(0, 0, 0)
        lights.push(directLight2)
        lights.push(directLight2.target)

        if (debug) {
            const helper1 = new THREE.DirectionalLightHelper(directLight1, 5)
            const helper2 = new THREE.DirectionalLightHelper(directLight2, 5)
            lights.push(helper1)
            lights.push(helper2)
        }

        callback(lights)
    }
}